* {
  margin: 0;
  padding: 0;
  color: white;
  text-decoration: none;
  user-select: none;
  touch-action: pan-y;
  box-sizing: border-box;
}
p {
  text-indent: 2em;
}
html,
body {
  background-color: #080258;

  .index {
    background: center / cover url("../images/index_bg.jpg");
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .list {
    background: url("../images/list_bg.jpg") no-repeat, #080258;
    background-size: 100% auto;
    background-position: top;
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    .listcontainer {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      .tip {
        width: 44%;
        margin: 3%;
        text-align: center;
        line-height: 30vw;
      }
      .item {
        width: 44vw;
        height: 30vw;
        display: block;
        border: 2px solid #00d0ff;
        border-radius: 15px;
        margin: 3vw;
        .circ {
          border: 2px solid #00d0ff;
          border-radius: 50%;
          width: 25vw;
          height: 25vw;
          margin: 2vw auto;
          background-color: #022f60;
          text-align: center;
          :first-child {
            line-height: 12vw;
            font-size: 3vw;
          }
          :last-child {
            line-height: 9vw;
            font-size: 6vw;
          }
        }
      }
    }
  }
  .detail {
    background: url("../images/list_bg.jpg") no-repeat, #080258;
    background-size: 100% auto;
    .circ {
      border-radius: 50%;
      width: 40vw;
      height: 40vw;
      margin: 4% auto;
      background-color: #0f1f74;
      text-align: center;
      :first-child {
        line-height: 18vw;
        font-size: 4vw;
      }
      :last-child {
        line-height: 10vw;
        font-size: 10vw;
      }
    }

    .content {
      h1 {
        margin: 2vw 0;
      }
      > div {
        margin: 0 5vw;
        padding: 5vw 0;
        &:not(:last-child) {
          border-bottom: 1px solid;
        }
      }
    }
    .vote {
      background-color: #11258b;
      border-radius: 3vw;
      margin: 5vw;
      padding: 5vw;
      h6 {
        font-size: 5vw;
        font-weight: normal;
      }
      h1 {
        text-align: center;
        font-size: 12vw;
        font-weight: normal;
      }
    }
  }
  .loading {
    height: 100%;
    width: 100%;
    text-align: center;
    line-height: 20em;
    font-size: 20px;
    font-weight: bold;
  }
  .logo {
    width: 80%;
    margin: 10px 10%;
  }
  .button {
    text-align: center;
    background-image: linear-gradient(#57a3f1, #0d62b9);
    color: white;
    display: block;
    margin: 10% auto;
    width: 80%;
    line-height: 38px;
    font-weight: bold;
    font-size: 18px;
    border-radius: 23px;
  }
  .disabled {
    pointer-events: none;
    background-image: linear-gradient(#959494, #7b7878);
  }
}
